<template>
    <div class="addBlogs">
      <h2>添加博客</h2>
      <form v-if="!submitted">
          <label>博客标题:</label>
          
          <input type="text" v-model="blog.title" required >
          <br>
      
          <label>博客内容:</label>
          <br>
          <textarea v-model="blog.content"></textarea>
          <div>
              <label>Vue.js</label>
              <input type="checkbox" value="Vue.js" v-model
              ="blog.categories">
              <label>Node.js</label>
              <input type="checkbox" value="Node.js"  v-model
              ="blog.categories">
              <label>React.js</label>
              <input type="checkbox" value="React.js"  v-model
              ="blog.categories">
              <label>Angular.js</label>
              <input type="checkbox" value="Angular.js"  v-model
              ="blog.categories">
              <br>
              
          </div>
          <label>作者：</label>
              <select v-model="blog.author"  >
                  <option v-for="author in authors"  >
                      {{author}}
                  </option>
              </select>
              <br>
              <button v-on:click.prevent="post" >添加博客</button>
      </form>
      <div v-if="submitted">
          你好：你的博客添加成功
      </div>
      <hr>
      <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容：</p>
        <p>{{blog.content}}</p>
        <p>博客分类</p>
        <ul>
            <li v-for="category in blog.categories">
                {{category}}
            </li>

        </ul>
        <label>作者：{{blog.author}}</label>
      </div>
    </div>
    
  </template>
  
  <script>
  export default {
    name: 'addBlogs',
    data () {
      return {
       blog:{
           title:"111",
           content:"",
           categories:[],
           author:""
       },
       authors:["张三","王二","麻子"],
       submitted:false
      }
    },
    methods:{
        post:function(){
                this.$http.post(
                    "https://jsonplaceholder.typicode.com/posts",
                    {
                        title:this.blog.title,
                        body:this.blog.content,
                        userId:1
                    }
                    
                )
                .then(function(data){
                    console.log
                    this.submitted=true
                })
        }
    }
    
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  
  </style>
  


