<template>
    <div id="Show-Blogs" v-theme:colunm="'narrow'" >
        
      <h1>show - blogs</h1>
      <input type="text" placeholder="搜索" v-model="serch">
      <div v-for="blog in filterBlogs" >
          <router-link v-bind:to="'/test2/blog/'+blog.id" ><h2 v-rainbow >{{blog.title | to-uppercase }}</h2></router-link>
          <article>{{blog.body | snippt}}</article>
      </div>
      
    </div>
  </template>
  
  <script>
  
  
  export default {
    name: 'Show-Blogs',
    data(){
        return{
            blogs:[],
            serch:""
        }
    },

    
    created(){
        this.$http.get(
            "./../static/post.json"
        ).then(function(data){
                this.blogs=data.body.slice(0,10)

        })
    },
    computed:{
        filterBlogs:function(){
            return this.blogs.filter((blog)=>{
                return blog.title.match(this.serch)
            })
        }
    }
            
}
    

  
  </script >
  
  <style scoped>
  
  
  </style>
  


